body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.WelcomeBG{
  max-width: 100%
}
.Welcome{
  font-size: 19px;
}
.welcomeLeft{
  float: "left";
  width: "40%";
  margin-left:10%;   
}
.Logo{
  margin-top: 3%;
}
.navbar-nav.mr-auto.nav-fill{
  width: 60%;
   text-align: center;
   margin-left: 10%;
}
.welcomeContent{
  margin-top: 35%;
  margin-bottom: 72%;
}
.welcomeContent > p{
  font-size: 1vw;
  color: #bfbebe;;
}
.welcomeContent > h1{
  font-size: 1.25vw;
  color: #bfbebe;;
}
.welcomeContent > h2{
  font-size: 3.3vw;
    font-weight: 200;
    color: #7cc576;
}
.welcomeContent > h5{
  font-size: 3.0vw;
  font-weight: 700;
  color: #7cc576;
  padding-bottom: 6%;
}
.bestDeal{
  text-align: center;
    width: 70%;
    float: left;
    padding-left: 30%;
}
.bestDeal>h2{
  font-size: 2vw;
    color: #525252;
    padding-bottom: 1%;
}
.bestDeal>p{
  color:#bfbebe;
}
.BestDealCard{
  display:grid;
  grid-template-columns: 33.33fr 33.33fr 33.33fr;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  grid-gap: 2.5em;
  text-align:center;
}
.Card{
  height: 34vw;
    border-radius: 1.7vw;
    background-color: #7cc576;
    margin-top:30%;
    margin-bottom: 50%;
    box-shadow: 0vw 0vw 2vw #28a74573;
}
.CardImage{
  margin-top: -20%;
  height: 27vw;
}
.BabyCareCard{
  display:grid;
  grid-template-columns: 33.33fr 33.33fr 33.33fr;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 8%;
  grid-gap: 2.5em;
  text-align:center;
}
.CardBaby{
  height: 20vw;
  width: 17vw;
  margin: auto;
  margin-top: 23%;
}

.ArticleCards{
  display:grid;
  grid-template-columns: 33.33fr 33.33fr 33.33fr;
  width: 80%;
  grid-gap: 2.5em;
  margin-left: 10%;
  margin-right: 10%;
  text-align:center;
  
}
.ArticleContainer{

  margin-top:30%;
    margin-bottom: 20%;
}
.ArticleCard{
     width: 25vw;
     height:40vw;
    border-radius: 2vw;
    background-color: white;
    
    box-shadow: 0vw 0vw 1.5vw #3c3b3b;
}
.ArticleInfo{
  margin: 7%;
}

.BabyBoss{
  text-align: center;
    padding-top: 5%;
    padding-left: 25%;
    padding-right: 25%;
    padding-bottom: 5%;
    color: white;
}
.BabyBoss >div{
  height: 0.4vw;
  width: 35%;
  background-image: linear-gradient(45deg, white, transparent);
  /* float: left; */
  margin-left: 35%;
}
.BabyBoss>p{
  font-size: 1.95vw;
    margin-top: 5%;
    font-weight: 100;
    color: hsla(0, 0%, 100%, 0.61);
}
.BabyBoss>h5{
  font-size: 2.1vw;
    font-weight: 700;
}

.Footer{
    display: grid;
    grid-template-columns: 23.33fr 23.33fr 23.33fr 30fr;
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    grid-gap: 2.5vw;
}

.Footer>div>h2{
  text-align: start;
    font-size: 1.25vw;
  color: #444444eb;
}
.Footer>div>ul{
  list-style: none;
  width: 100%;
  display: contents;
  text-align: start;
  margin: auto;
    
}
.Footer>div>ul>a{
  color : #2f2f2fd4;
  font-size: 1vw;
}

.ReviewCard{
  margin: 1.5%;
  width: 28vw;
  height: 18vw;
  border-radius: 1.8vw;
  padding: 1%;
  box-shadow: 0vw 0vw 1.5vw #cececed9;
}
.ReviewCard >h2{
  font-size: 1.5vw;
}
.ReviewCard >p{
  font-size: 1vw;
}
.social{
  display: grid;
    grid-template-columns: 10fr 10fr 10fr 10fr 25fr;
}

